<template>
    <div class="product">
      <img src="./product.png" alt="">
    </div>
</template>

<script>
  export default {
    name: 'Product'
  };
</script>

<style lang="scss" scoped>
@import "~assets/scss/mixins";
.product {
   overflow: hidden;
   position: absolute;
   top: 0;
   left: 0;
   z-index: $product-z-index;
   width: 100%;
   height: 100%;
   background-color: $bgc-theme;
   img{
     display: block;
     width: 100%;
   }
 }
</style>



